export const typeDefs = `
    type SiteEntryOutput {
        quantity: Int!,
        cost: Int!
    }
    input SiteEntryInput {
        quantity: Int!,
        cost: Int!
    }
`;
export const defaultState = {
    networkStatus: {
      __typename: 'NetworkStatus',
      isConnected: true,
    },
    localLogin: {
        __typename: 'LocalLogin',
        token: window.localStorage.getItem('userToken'),
    },
    siteEntryTable: {
        __typename: 'SiteEntryTable',
        order: 'asc',
        orderBy: 'createdAt',
        selected: [],
        page: 0,
        rowsPerPage: 15,
        dialogOpen: false,
    },
    userEntryTable: {
        __typename: 'UserEntryTable',
        order: 'desc',
        orderBy: 'id',
        selected: [],
        page: 0,
        rowsPerPage: 15,
        dialogOpen: false,
    }
};

export const resolvers = {
    Mutation: {
        updateNetworkStatus: (_, { isConnected }, { cache }) => {
            const data = {
                networkStatus: {
                    __typename: 'NetworkStatus',
                    isConnected
                },
            };
            cache.writeData({ data });
            return null;
        },
        localLogin: async (_, { token }, { cache }) => {
            console.log("local login mutation resolver called....");
            const data = {
                localLogin: {
                    __typename: 'LocalLogin',
                    token
                },
            };
            await cache.writeData({ data });
            return null;
        },
        siteEntryTable: async (_, args, { cache }) => {
            console.log("site table mutate function called...");
            // const prevData = cache.readData();
            // console.log("prevData...", prevData);
            console.log("args...", args);
            const data = {
                siteEntryTable: {
                    __typename: 'SiteEntryTable',
                    ...args
                },
            };
            await cache.writeData({ data });
            return null;
        },
        userEntryTable: async (_, args, { cache }) => {
            console.log("user mutate function called...");
            // const prevData = cache.readData();
            // console.log("prevData...", prevData);
            console.log("args...", args);
            const data = {
                userEntryTable: {
                    __typename: 'UserEntryTable',
                    ...args
                },
            };
            await cache.writeData({ data });
            return null;
        }
    },
};



